/* Variables  */

body {
    --main-brand-color: #B76944
}

/* General Styles */

.App{
    font-family: 'Raleway', sans-serif;
}

/* Transitions */

.color-trans {
    transition: color 2s;
    -webkit-transition: color 2s;
    -moz-transition: color 2s;
    -ms-transition: color 2s;
    -o-transition: color 2s;
}

.bg-trans {
    transition: background-color 2s;
    -webkit-transition: background-color 2s;
    -moz-transition: background-color 2s;
    -ms-transition: background-color 2s;
    -o-transition: background-color 2s;
}

.fadeIn {
    animation: fadeIn 1s ease-in;
    -webkit-animation: fadeIn 1s ease-in;
} 

.fadeOut {
    animation: fadeOut 1s ease-in;
    -webkit-animation: fadeOut 1s ease-in;
} 

/* Animations */


@-webkit-keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}


@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes fadeOut{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}

@keyframes fadeOut{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
.quote__box{
    width:300px;
    padding: 20px 40px;
    text-align: center;
    background-color: #f2f2f2;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    transition:max-height 3s;
    -webkit-transition:max-height 3s;
    -moz-transition:max-height 3s;
    -ms-transition:max-height 3s;
    -o-transition:max-height 3s;
}

.quote__text {
    font-size: 1.5em;
    margin-bottom:0.3em;
    
}

.quote__author{
    text-align: end;
    animation:fade 1s ease-in;
    -webkit-animation:fade 1s ease-in;
}

.quote__action {
    margin-top:20px;
    color: #f2f2f2;
}

.quotes__action--social a{
    color: #f2f2f2;
    margin-right: 5px;
    background-color: var(--main-brand-color);
}

.quotes__action--social a:hover{
    color: #f2f2f2;
    background-color: var(--main-brand-color);
}


.qoute__button{
    background-color: var(--main-brand-color);
    padding: 10px;
    border:none;
    font-size: 14px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    color: #f2f2f2;
    transition:background-color 2s;
    outline:none
    
}

.qoute__button:hover{
    opacity: 0.9;
    outline:none
    
}

.qoute__button:focus{
    opacity: 0.9;
    outline:none
    
}

.quote__button--tumblr{
    padding: 10px 13px
}

button.qoute__button{
    font-size:12px
}

@media only screen and (min-width: 600px) {
    .quote__box{
        width:450px
    }
  }
